import React, { useState } from "react";
import Device, { DEVICES } from "./Device";
import "./App.css";
import Toolbar from "./Toolbar";
import PhonesList from "./PhonesList";
import Form from "./Form";

function App() {
  const [zoom, setZoom] = useState(0.7);
  const [selectedPhone, setSelectedPhone] = useState("IPHONE8");
  const [color, setColor] = useState(undefined);
  const [landscape, setLandscape] = useState(false);
  const [src, setSrc] = useState("");

  const handleChangeDevice = e => {
    setSelectedPhone(e.target.name);
  };
  const handleZoomIn = () => {
    if (zoom >= 1) return;
    setZoom(zoom => zoom + 0.1);
  };

  const handleZoomout = () => {
    if (zoom <= 0.3) return;
    setZoom(zoom => zoom - 0.1);
  };

  const handleLandScapeToggle = () => {
    setLandscape(landscape => !landscape);
  };

  const handleChangeColor = e => {
    setColor(e.target.name);
  };
  const handleSubmit = src => {
    setSrc(src);
  };
  return (
    <div className="container">
      <Toolbar
        onZoomOut={handleZoomout}
        onZoomIn={handleZoomIn}
        onLandScapeToggle={handleLandScapeToggle}
        showLandScape={DEVICES[selectedPhone].hasLandscape}
        isLandScape={landscape}
      />
      <Form onSubmit={handleSubmit} />
      <div className="main-container">
        <div className="phone-container">
          <Device
            type={selectedPhone}
            color={color}
            zoom={zoom}
            landscape={landscape}
            src={src}
            title="Activity"
          />
        </div>
        <PhonesList
          onChangeColor={handleChangeColor}
          devices={DEVICES}
          onDeviceChange={handleChangeDevice}
          selectedPhone={selectedPhone}
        />
      </div>
    </div>
  );
}

export default App;
