import React from "react";
import {
  StayCurrentLandscape,
  StayCurrentPortrait,
  ZoomIn,
  ZoomOut
} from "@styled-icons/material";

const Toolbar = props => {
  const {
    onZoomIn,
    onZoomOut,
    showLandScape,
    isLandScape,
    onLandScapeToggle
  } = props;
  return (
    <div className="toolbar">
      <ZoomIn
        onClick={onZoomIn}
        className="toolbar-item"
        size={30}
        fontSize="30px"
      />
      <ZoomOut onClick={onZoomOut} className="toolbar-item" size={30} />
      {showLandScape &&
        (isLandScape ? (
          <StayCurrentPortrait
            onClick={onLandScapeToggle}
            className="toolbar-item"
            size={30}
          />
        ) : (
          <StayCurrentLandscape
            onClick={onLandScapeToggle}
            className="toolbar-item"
            size={30}
          />
        ))}
    </div>
  );
};

export default Toolbar;
