const Device = props => {
  if (DEVICES[props.type]) {
    return DEVICES[props.type].render({
      ...props,
      title: `${props.title} on ${DEVICES[props.type].a11yTitle}`
    });
  }
  return <p>Device not found! </p>;
};

export default Device;
